.sidebar {
    color: white;
    flex: 0.3;
    background-color: var(--header-color);
    border-top: 1px solid #49274b;
    max-width: 260px;
}

.sidebar>hr {
    margin: 10px 0;
    border: 1px solid #49274b;
}

.sidebarHeader {
    display: flex;
    border-bottom: 1px solid #49274b;
    padding-bottom: 10px;
    padding: 13px;
}

.sidebarInfo {
    flex: 1;
}

.sidebarInfo>h2 {
    font-size: 15px;
    font-weight: 900;
    margin-bottom: 5px;
}

.sidebarInfo>h3 {
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: 400;
}

.sidebarInfo>h3>.MuiSvgIcon-root {
    font-size: 14px;
    margin-top: 1px;
    margin-right: 2px;
    color: green;
}

.sidebarHeader>.MuiSvgIcon-root {
    padding: 8px;
    color: #49274b;
    font-size: 18px;
    background-color: white;
    border-radius: 1000px;
}