.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    background-color: var(--header-color);
    color: #ffffff;
}

.headerLeft {
    flex: 0.3;
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.headerLeft>.MuiSvgIcon-root {
    margin-left: auto;
    margin-right: 30px;
}

.headerSearch {
    flex: 0.4;
    background-color: #421f44;
    text-align: center;
    display: flex;
    padding: 0 50px;
    color: gray;
    border: 1px solid gray;
    border-radius: 6px;
}

.headerSearch>input {
    background-color: transparent;
    color: #ffffff;
    border: none;
    text-align: center;
    min-width: 35vw;
}

.headerRight {
    flex: 0.3;
    display: flex;
    align-items: flex-end;
}
.headerRight>.MuiSvgIcon-root{
    margin-left: auto;
    margin-right: 20px;
}